// 메뉴바 컬러
@mixin toggleMenuColor($color) {
    @if $color ==W {
        .burger-sticks {
            background: #ffffff;
        }

        .burger-sticks::after {
            background: #ffffff;
        }

        .burger-sticks::before {
            background: #ffffff;
        }
    }

    @if $color ==D {
        .burger-sticks {
            background: #222222;
        }

        .burger-sticks::after {
            background: #222222;
        }

        .burger-sticks::before {
            background: #222222;
        }
    }

}

// 헤더 컬러
@mixin headerMode($bgColor, $isBorder, $mode) {
    width: 100vw;
    height: 71px;
    position: fixed;
    top: 0;
    left: 0;
    background: $bgColor;
    z-index: 9999;
    transition: all 0.3s;

    @if $isBorder ==true {
        border-bottom: 1px solid #F2F2F2;
    }

    @if $isBorder ==false {
        border-bottom: none;
    }

    @include responsive(TABLET) {
        height: 61px;
    }

    nav.wrap {
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include responsive(TABLET) {
            display: block;
        }

        div.header-item-wrapper {

            @include responsive(TABLET) {
                height: 61px;
                position: relative;
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
            }

            .header-logo-img {}

            span.header-button-mobile {
                display: none;

                @if $mode ==scrolled {
                    @include toggleMenuColor(D);
                }

                @if $mode ==defaulted {
                    @include toggleMenuColor(W);
                }

                @include responsive(TABLET) {
                    display: block;
                }
            }
        }

        ul.header-menu-group {
            display: flex;
            transition: all 0.4s;
            border-radius: 0px 0px 10px 10px;
            @include responsive(TABLET) {
                background: #ffffff;
                flex-direction: column;
                width: 100%;
                box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
                position: fixed;
                top: -400px;
                left: 0;

                &.active {
                    top: 60px;
                }
            }


            li {

                font-family: 'Spoqa Han Sans Neo', 'sans-serif';
                margin-left: 1rem;
                font-size: 1rem;
                font-weight: 600;

                cursor: pointer;

                @if $mode ==defaulted {
                    color: #ffffff;
                }

                @if $mode ==scrolled {
                    color: #383838;
                }

                @include responsive(TABLET) {
                    padding: 20px;
                    text-align: left;
                    font-size: 0.9rem;
                    font-weight: 400;
                    color: gray;
                }

            }
        }
    }
}