@mixin responsive($screen) {
    //~1080
    @if($screen == 'TABLET') {
        @media screen and (max-width:$lg-break-point) {
            @content;
        }
    }
    // ~768
    @if($screen == 'MOBILE-LG') {
        @media screen and (max-width:$md-breakpoint) {
            @content;
        }
    }
   // ~480
    @if($screen == 'MOBILE-MD') {
        @media screen and (max-width:$sm-break-point) {
            @content;
        }
    }

}



