@import '../stylemodules/main.scss';
@import './animation.scss';

.header.default {
    @include headerMode(none, false, defaulted);
}

.header.scrolled {
    @include headerMode(#FFFFFF, true, scrolled);
    z-index: 9999;
}

.header:hover {
    @include headerMode(#FFFFFF, false, defaulted);
    @include headerMode(#FFFFFF, true, scrolled);
}