@import '../../stylemodules/main.scss';

.solution-background {
    // 메인 영상
    main.ziple-video {
        width: 100vw;
        height:100vh;
        overflow:hidden;
        position: relative;
        @include responsive(TABLET) {
            height: 87vh;
        }

        @include responsive(MOBILE-LG) {
            height: 80vh;
        }
        p.ziple-main-title {
            position:absolute;
            z-index:100;
            color:#ffffff;
            top: 200;
            left:50%;
            top:50%;
            transform: translate(-50%, -50%);

            h2 {
                text-align:left;
                font-size:3rem;
                font-weight:700;
                line-height:60px;
                @include responsive(TABLET) {
                  font-size:$lg-font-size-d;
                  line-height:50px;
                }
    
                @include responsive(MOBILE-LG) {
                    font-size:$lg-font-size-t;
                    line-height:40px;
                }
                @include responsive(MOBILE-LG) {
                    font-size:$lg-font-size-m;
                    line-height:35px;
                }
               
            }
        }
        video {
            filter: blur(0.6px);
            width:100vw;
            position:absolute;
            left:0;
            transition:all 0.2s;    
            height: 100%;
            margin: 0;
            object-fit:fill;
            @include responsive(TABLET) {
                height: 87%;
            }

            @include responsive(MOBILE-LG) {
                height: 80%;
            }
        
        }
    }

    // 자이플 솔루션 소개
    .solution-info {
        padding:120px 0;
    }
}